.btn-details {
    @extend %p-big;
    @extend %main-border-radius;
    
    text-decoration: none;

    width: 199px;
    padding: 7px 20px;
    margin: -7px -20px;

    display: flex;
    align-items: center;
    gap: 15px;


    background-color: transparent;
    transition: all 0.3s ease;

    @media (max-width: 768px) {
        width: 163px;
    }

    &__text {
        color: $white;
    }

    &__icon {
        width: 25px;
        height: 26px;
        background-image: url('data:image/svg+xml,%3Csvg width="22" height="16" viewBox="0 0 22 16" fill="none" xmlns="http://www.w3.org/2000/svg"%3E%3Cpath id="Vector" d="M21.7004 7.28331L15.1535 0.796818C14.7541 0.401061 14.1063 0.401061 13.7068 0.796818C13.3073 1.19266 13.3073 1.83433 13.7068 2.23017L18.5074 6.98647H1.02294C0.458032 6.98647 0 7.44029 0 7.99999C0 8.55961 0.458032 9.0135 1.02294 9.0135H18.5074L13.707 13.7698C13.3075 14.1656 13.3075 14.8073 13.707 15.2032C13.9067 15.4009 14.1686 15.5 14.4304 15.5C14.6921 15.5 14.9539 15.4009 15.1537 15.2032L21.7004 8.71667C22.0999 8.32083 22.0999 7.67915 21.7004 7.28331Z" fill="white"/%3E%3C/svg%3E');
        background-repeat: no-repeat;
        background-position: center;
    }

    @media(any-hover: hover) {
        &:hover {
            background-color: $green400;
        }

        &:active {
            background-color: $orange200;
        }
    }

}